@charset "UTF-8";
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

body {
  font-family: Arial;
}

a {
  display: flex;
  justify-content: center;
}

.title {
  text-align: center;
  font-size: 18px;
  line-height: 21px;
  letter-spacing: 0px;
  color: #3E4C69;
}

.subtitle {
  font-size: 12px;
  line-height: 14px;
  letter-spacing: 0px;
  color: #661600;
  margin-bottom: 5px;
  font-weight: 500;
}

.text {
  font-size: 12px;
  line-height: 15px;
  letter-spacing: 0px;
  color: #0D0D0D;
}

.text-checkbox {
  padding-left: 15px;
  position: relative;
}

.text-checkbox:before {
  content: '';
  display: block;
  position: absolute;
  top: 50%;
  left: 0;
  transform: translate(0, -50%);
  width: 10px;
  height: 10px;
  border: 1px solid #3E4C69;
  border-radius: 1px;
}

.text-checkbox_true:after {
  content: '';
  display: block;
  position: absolute;
  top: calc(50% + 2px);
  left: 1px;
  transform: translate(0, -50%);
  width: 10px;
  height: 10px;
  background-image: url("/img/cross.png");
  background-size: 7px 7px;
  background-repeat: no-repeat;
}

.text-footer {
  color: white;
  font-size: 10px;
  line-height: 15px;
}

.row {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.col {
  min-width: 130px;
}

.frame {
  border: 1px solid #3E4C69;
  border-radius: 2px;
  padding: 10px;
  width: 100%;
  min-height: 100px;
}

.frame_sm {
  max-width: 250px;
}

.frame_lg {
  max-width: 410px;
}

.frame_md {
  max-width: 315px;
  padding: 5px;
  margin-right: 20px;
}

.frame__row {
  justify-content: flex-start;
  align-items: flex-start;
  margin-top: 15px;
}

.lines {
  margin-top: -15px;
}

.line {
  display: flex;
  align-items: center;
  justify-content: center;
  letter-spacing: 0px;
  color: #FFFFFF;
}

.line_blue {
  width: 300px;
  height: 30px;
  background-color: #3E4C69;
  text-align: center;
  font-size: 18px;
  line-height: 21px;
  font-weight: bold;
  margin-bottom: 60px;
}

.line_red {
  width: 320px;
  height: 40px;
  background: #661600;
  margin-bottom: 10px;
  font-size: 24px;
  line-height: 28px;
}

.header {
  display: flex;
  justify-content: flex-end;
  padding: 25px 50px 0 50px;
}

.header__logo {
  width: 200px;
  height: 50px;
  object-fit: contain;
}

@font-face {
  font-family: "iconfont";
  src: url("../fonts/iconfont.eot");
  src: url("../fonts/iconfont.eot?#iefix") format("eot"), url("../fonts/iconfont.woff2") format("woff2"), url("../fonts/iconfont.woff") format("woff"), url("../fonts/iconfont.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}

.icon {
  font-family: "iconfont";
  font-style: normal;
  font-weight: normal;
  text-rendering: auto;
  speak: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon {
  display: inline-block;
}

.icon-facebook:before {
  content: "Ó®Å";
}

.lastschrift {
  padding: 80px 50px 290px;
}

.lastschrift__title {
  margin-bottom: 30px;
}

.lastschrift__row {
  margin-bottom: 40px;
}

.lastschrift__subtitle, .lastschrift__text {
  margin-bottom: 15px;
}

.lastschrift__bottom {
  margin-top: 80px;
  justify-content: flex-start;
}

.zusammenfassung__title {
  margin-bottom: 15px;
}

.zusammenfassung__subtitle {
  text-align: center;
  margin-bottom: 40px;
  font-size: 14px;
  line-height: 16px;
}

.zusammenfassung__details {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

.zusammenfassung__content {
  padding: 0 50px;
}

.zusammenfassung__text {
  margin-bottom: 10px;
}

.zusammenfassung__footer {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  background-color: #3E4C69;
  padding: 20px 50px 45px;
  margin-top: 300px;
}

.firmen {
  padding: 30px 50px 250px;
}

.firmen__header {
  padding-bottom: 10px;
  border-bottom: 5px solid #3E4C69;
}
